export default "@import url('https://fonts.googleapis.com/css2?family=Ubuntu:wght@500&display=swap');\n\n.header {\n    align-items: center;\n    background: linear-gradient(orange 10%, 80%, #FFD280 100%);\n    display: flex;\n    justify-content: space-between;\n    margin: 0;\n    padding: 20px;\n}\n\n.fa-database {\n    margin-right: 10px;\n}\n\n.header__title a {\n    color: black;\n    font-family: Ubuntu;\n    font-size: 1.8rem;\n    text-decoration: none;\n}\n\n.navigation-menu {\n    display: flex;\n    justify-self: end;\n    list-style: none;\n}\n\n.navigation-menu li a {\n    color: black;\n    padding: 10px 30px;\n    text-decoration: none;\n    transition-duration: 0.2s;\n}\n\n.navigation-menu li:not(:last-child) a {\n    border-right: 1px solid black;\n}\n\n.navigation-menu li a:hover {\n    background-color: #FFD280;\n    cursor: pointer;\n}\n\n.hamburger-menu-icon {\n    color: black;\n    display: none;\n    flex-grow: 1;\n    text-align: right;\n    text-decoration: none;\n}\n\n.fa-times {\n    display: none;\n}\n\n/* Hamburger Menu */\n\n.hamburger-menu {\n    background-color: orange;\n    display: none;\n    flex-direction: column;\n}\n\n.hamburger-menu a {\n    border-bottom: 1px solid #FFD280;\n    color: black;\n    padding: 10px 20px;\n    text-decoration: none;\n    transition-duration: 0.2s;\n}\n\n.hamburger-menu a:hover {\n    background-color: #FFD280;\n}\n\n.introductory-text-container {\n    background-color: yellow;\n    padding: 20px;\n}\n\n@media screen and (max-width: 600px){\n\n    .header__title a {\n        font-size: 1.2rem;\n    }\n}\n\n@media screen and (max-width: 800px){\n\n    .navigation-menu {\n        display: none;\n    }\n\n    .hamburger-menu-icon {\n        display: block;\n    }\n}\n/*# sourceMappingURL=data:application/json;base64, */"